import React from 'react';
import Content from './Content';
import RightContent from './RightContent';
import Footer from './Footer';
class MemeGenerator extends React.Component{
    constructor(){
        super()
        this.state={
            topText:"",
            bottomText:"",
            randomImage:"data:image/jpeg;base64,",
            allMemeImages:[],
            color:"white"
        }
        this.handleChange=this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleColor=this.handleColor.bind(this);
    }
     componentDidMount(){
         fetch("https://api.imgflip.com/get_memes")
            .then(response => response.json())
            .then(response=>{
                const {memes}=response.data
                console.log([memes[0]])
                this.setState({
                    allMemeImages:memes
                })
            })
        }

        handleChange(event){
            const {name, value}= event.target
            this.setState({
                [name]:value,})
         }
        handleColor(){
            if(this.state.color==="white"){this.setState({color:"black"})}
            else{
            this.setState({color:"white"})
        }
        }

        handleSubmit(event){
            event.preventDefault();
            const randomNumber=Math.floor(Math.random()*this.state.allMemeImages.length)
            const randMemeImg=this.state.allMemeImages[randomNumber].url
            this.setState({
                randomImage:randMemeImg
            })
        }

    render(){
        
        return(<div className="container1">
            <Content/>
            <RightContent/>
            <form className="meme-form" onSubmit={this.handleSubmit}>
                <input 
                type="text" 
                className="first"
                name="topText"
                 placeholder="topText"
                  value={this.state.topText}
                  onChange={this.handleChange}/>

                <br/>

                <input type="text"
                 className="seccond"   
                 name="bottomText"
                 placeholder="bottomText" 
                 value={this.state.bottomText}
                 onChange={this.handleChange}/>

                <br/>
                <button className="final">GEN..</button>

                <input type="radio"  name="color" onChange={this.handleColor}/>
                <label>BLACK</label>

                <input type="radio" name="color" onChange={this.handleColor}/>
                <label>WHITE</label>

                </form>      

                <div className="meme">

                <h2 
                className="top" 
                style={{color:this.state.color}} >{this.state.topText}</h2>
                <img src={this.state.randomImage} alt="meme" />

                <h2 
                className="bottom" 
                style={{color:this.state.color}}>{this.state.bottomText}</h2>    
                </div>  
                <Footer/>
         </div>)
    }
}


export default MemeGenerator;